const Sidebar = () => (
  <div className="sidebar">
    <div className="sidebar-title"></div>
    {[
      { to: "/", text: "" },
      { to: "/note", text: "" },
      { to: "/data", text: "" },
      { to: "/about", text: "" },
    ].map(({ to, text }) => (
      <div className="sidebar-box" key={to}>
        <Link to={to}>{text}</Link>
      </div>
    ))}
  </div>
);

